import imApi from "../../api/imApi";

export const fetchUD_Data = () => {
    return dispatch => {
            return imApi.fetchSellerUserdetails().then(response => {          
                //console.log(response);
                dispatch(fetchUDSuccess(response));
                return true;
            }).catch(error => {
                dispatch(fetchUDFailure(error.message))
            })
    }
}
  
export const fetchUDSuccess = (data) => {
    return {
        type: "FETCH_UD_SUCCESS",
        payload: data
    }
}
  
export const fetchUDFailure = (error) => {
    return {
        type: "FETCH_UD_FAILURE",
        payload: error
    }
}
  